.container {
  padding-left: 0px;
  padding-right: 0px;
  width: 100%;
}

.container .row {
  margin-left: 0px;
  margin-right: 0px;
  display: flex;
}

.container .row [class*="col-"] {
  padding-left: 0px;
  padding-right: 0px;
  box-sizing: border-box;
  margin-bottom: 0px;
}

.container .row .col-1-12 {
  width: 8.33333%;
}

.container .row .col-2-12 {
  width: 16.66667%;
}

.container .row .col-3-12 {
  width: 25%;
}

.container .row .col-4-12 {
  width: 33.33333%;
}

.container .row .col-5-12 {
  width: 41.66667%;
}

.container .row .col-6-12 {
  width: 50%;
}

.container .row .col-7-12 {
  width: 58.33333%;
}

.container .row .col-8-12 {
  width: 66.66667%;
}

.container .row .col-9-12 {
  width: 75%;
}

.container .row .col-10-12 {
  width: 83.33333%;
}

.container .row .col-11-12 {
  width: 91.66667%;
}

.container .row .col-12-12 {
  width: 100%;
}

.row .header {
  display: flex;
  justify-content: space-between;
}

.headerSitOnChair {
  background: #222;
  height: 41px;
}

.logoSitOnChair {
  color: #fff;
  height: 41px;
  padding-top: 12px;
  padding-left: 63px;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 13px;
}

.logoSitOnChair > .logo__link > span {
  color: #24BA9F;
  font-weight: 400;
  text-shadow: 0px 1px, 1px 0px, 1px 1px;
}

.nav-list {
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  letter-spacing: 1px;
  text-transform: uppercase;
  height: 41px;
  font-size: 13px;
  font-weight: 600;
  margin-right: 50px;
}

.nav-list__link,
.logo__link {
  text-decoration: none;
  color: white;
  display: block;
}

.nav-list.dropdown {
  display: none;
  position: absolute;
  background: black;
  left: -10px;
  top: 35px;
  color: white;
  text-transform: none;
}

.nav-list__subitem a {
  padding: 10px;
  background: black;
}

.nav-list__item.drop {
  position: relative;
}

.nav-list__item.drop:hover .nav-list.dropdown {
  display: block;
}

.menu-triangle {
  width: 0;
  margin-right: auto;
  margin-left: auto;
  border: 10px solid transparent;
  border-bottom-color: black;
  position: absolute;
  left: 30px;
  top: -18px;
}

.page-nav-toggle {
  width: 55px;
  height: 37px;
  border: none;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 6px;
  position: fixed;
  top: 2px;
  right: 15px;
  cursor: pointer;
  display: none;
  z-index: 100;
}

.page-nav-toggle strong {
  position: absolute;
  left: -99999px;
}

.page-nav-toggle span {
  width: 26px;
  height: 6px;
  background: #fff;
  display: block;
  margin: 3px auto;
}

@media (max-width: 860px) {
  .nav-list {
    display: none;
  }
  .headerSitOnChair .header {
    display: flex;
    text-align: center;
    align-content: center;
    padding-left: 70px;
  }
  .logoSitOnChair {
    font-size: 16px;
    font-weight: 600;
  }
  .page-nav-toggle {
    display: block;
  }
}

@media (max-width: 400px) {
  .responsive_small .logoSitOnChair {
    padding: 12px 0 0 0;
    margin: 0;
  }
}

.main__part {
  background: white;
  background: -moz-linear-gradient(top, white 0%, #f6f6f6 47%, #cccccc 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, white), color-stop(47%, #f6f6f6), color-stop(100%, #cccccc));
  background: -webkit-linear-gradient(top, white 0%, #f6f6f6 47%, #cccccc 100%);
  background: -o-linear-gradient(top, white 0%, #f6f6f6 47%, #cccccc 100%);
  background: -ms-linear-gradient(top, white 0%, #f6f6f6 47%, #cccccc 100%);
  background: linear-gradient(to bottom, white 0%, #f6f6f6 47%, #cccccc 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#cccccc', GradientType=0);
  height: 505px;
  display: flex;
}

.arrow__position {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.arrow--left,
.arrow--right {
  font-size: 85px;
  color: #24BA9F;
  border: none;
  background: rgba(255, 255, 255, 0);
  cursor: pointer;
}

.arrow--left:hover,
.arrow--right:hover {
  color: rgba(5, 126, 103, 0.8);
}

.main__chair__image {
  margin-top: 50px;
  margin-left: 10%;
  height: 100%;
  width: 65%;
}

.slider {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider > li {
  display: none;
}

li.visible {
  display: inline-block;
  width: 98%;
}

.slider li.visible > img {
  width: 100%;
  height: auto;
}

.description_sitOnChair {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
}

.description_sitOnChair > p span {
  color: #24BA9F;
  text-shadow: 0px 1px, 1px 0px, 1px 1px;
}

.description_sitOnChair p:first-child {
  text-align: center;
  text-transform: uppercase;
  font-size: 38px;
  font-weight: 800;
  align-self: center;
  margin-top: 130px;
}

.description_sitOnChair p:nth-child(2) {
  font-size: 14px;
  line-height: 1.5em;
  text-align: center;
  word-spacing: 0.5em;
  align-self: center;
  width: 70%;
}

.description__button {
  width: 250px;
  height: 55px;
  box-shadow: none;
  border-radius: 10px;
  font-size: 22px;
  letter-spacing: 0.1em;
  background-color: #24BA9F;
  color: white;
  text-transform: uppercase;
  align-self: center;
  margin-bottom: 100px;
}

.description__button:hover {
  cursor: pointer;
  background-color: lightgray;
  color: #24BA9F;
}

@media (max-width: 1090px) {
  .arrow--left,
  .arrow--right {
    font-size: 60px;
  }
  .description_sitOnChair p:first-child {
    font-size: 28px;
  }
  .description_sitOnChair p:nth-child(2) {
    font-size: 13px;
  }
  .description__button {
    font-size: 16px;
    width: 200px;
    height: 45px;
  }
}

@media (max-width: 860px) and (max-width: 720px) {
  .arrow--left,
  .arrow--right {
    display: none;
  }
  .description_sitOnChair p:first-child {
    font-size: 20px;
  }
  .description_sitOnChair p:nth-child(2) {
    font-size: 12px;
    line-height: 2em;
  }
  .description__button {
    font-size: 14px;
    width: 170px;
    height: 40px;
  }
}

@media (max-width: 860px) and (max-width: 620px) {
  .description__button {
    display: none;
  }
  .description_sitOnChair {
    justify-content: flex-start;
  }
  .description_sitOnChair p:first-child {
    margin-top: 100px;
    font-size: 17px;
  }
  .description_sitOnChair p:nth-child(2) {
    font-size: 12px;
    width: 100%;
    margin-top: 20px;
  }
}

