.price_section {
  margin-top: 50px;
}

.price_section .price_header {
  font-size: 25px;
  display: block;
  border-bottom: 5px solid lightseagreen;
  width: 180px;
  margin: 15px 0 70px 50px;
  color: #474242;
  text-transform: uppercase;
  font-weight: 600;
}

.price_section .container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.price_section .background-active {
  background: lightseagreen;
  border: 1px solid lightseagreen;
}

.price-background {
  padding-top: 30px;
  background: #f2f2f2;
  margin: 0 auto;
}

.price-plan {
  height: 500px;
  width: 30%;
  min-width: 250px;
  border: 2px solid lightgray;
  background: white;
  margin: 0 10px 50px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.price-plan .circle {
  position: absolute;
  width: 50px;
  height: 50px;
  border: 2px solid lightgray;
  background: white;
  top: -25px;
  left: 40%;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.price-plan .circle .smaller-circle {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: lightgray;
}

.price-plan .circle-active {
  position: absolute;
  width: 50px;
  height: 50px;
  background: lightseagreen;
  top: -25px;
  left: 40%;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.price-plan .circle-active .smaller-circle-active {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: white;
}

.price-plan .price-plan-header {
  padding: 30px 0 30px 0;
  font-size: 25px;
  color: lightseagreen;
  font-weight: bold;
}

.price-plan .font-white {
  color: white;
}

.price-plan .price-of-plan {
  border: 2px solid lightgray;
  border-radius: 50%;
