  width: 130px;
  height: 130px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.price-plan .price-of-plan h2 {
  font-size: 40px;
  color: gray;
}

.price-plan .price-of-plan h2 sup {
