  width: 100%;
  text-align: center;
  padding: 5px 0 5px 0;
  color: gray;
}

.price-plan .features-lighter {
  background: #ededed;
  font-size: 25px;
  width: 100%;
  text-align: center;
  padding: 5px 0 5px 0;
  color: gray;
}

.price-plan .plan-choice {
  margin-bottom: 15px;
  font-size: 15px;
  color: white;
  width: 125px;
  height: 35px;
  background: lightseagreen;
  border: 1px solid lightseagreen;
