}

.price-plan .plan-choice:hover {
  cursor: pointer;
  background-color: lightgray;
  color: #24BA9F;
}

.price-plan .first {
  margin-top: 110px;
}

.price-plan .second {
  margin-top: 70px;
  background: white;
  color: lightseagreen;
}

.price-plan .third {
  margin-top: 31px;
}

.price-plan .background-white {
  background: white;
}

@media (max-width: 1240px) {
