.order-form {
    .container {
        background: #f4f4f4;
    }
}

.chair-choice {
    display: flex;
    flex-wrap: wrap;
    margin-top: 50px;

    h1 {
        font-size: 18px;
        padding-top: 30px;
        display: block;
        color: gray;
        border-bottom: 5px solid lightseagreen;
        margin-bottom: 50px;
    }
}

.order-choice {
    display: flex;
    flex-direction: column;
    width: 30%;
    min-width: 280px;
    padding-left: 25px;

    select {
        width: 100%;
        height: 60px;
        font-size: 25px;
        color: gray;
        margin-bottom: 20px;
        background: white;
        border: 2px solid lightseagreen;
    }

}

.transport {
    display: flex;
    font-size: 25px;
    color: gray;
    align-items: center;
    margin-bottom: 15px;
}

#transport {
    width: 50px;
    height: 50px;
    margin-right: 15px;
}

.order-img {
    margin-top: 80px;
    width: 30%;
    min-width: 390px;
    height: 280px;
    background-size: 70% 70%;
    background: url("../images/red_chair.png") no-repeat center;
}

.order-summ {
    width: 30%;
    min-width: 280px;

    h1 {
        text-align: right;
    }

    .order-box {
        background: white;
        padding: 7px;
    }
}

.order-box-specifies {
    display: flex;
    height: 160px;
    border-bottom: 2px solid lightgray;

    .chair-type {
        display: flex;
        flex-direction: column;
        color: gray;
        width: 70%;
        border-right: 2px solid lightgray;

        h2 {
            color: lightseagreen;
            font-size: 20px;
        }

        span {
            margin: 5px 0 5px 0;
        }
    }

    .chair-type-costs {
        display: flex;
        flex-direction: column;

        span {
            padding-left: 80px;
            margin-bottom: 10px;
            color: lightseagreen;
            font-weight: bold;
            font-size: 15x;
        }
    }
}

.order-box-summ {
    display: flex;
    justify-content: space-between;
    padding: 4px;

    h3 {
        color: gray;
    }

    p {
        font-size: 25px;
        color: lightseagreen;
    }
}

.place-order {
    width: 190px;
    height: 40px;
    font-size: 18px;
    color: white;
    background: lightseagreen;
    border-radius: 6px;
    border: 1px solid lightseagreen;
    margin: 20px 0 20px 0;

    &:hover {
        cursor: pointer;
        background-color: lightgray;
        color: #24BA9F;
    }
}

@media (max-width: 1010px) {
    .chair-choice {
        justify-content: space-around;

        h1 {
            font-size: 15px;
        }

        .order-summ {
            width: 100%;

            h1 {
                width: 40%;
                text-align: left;
            }
        }
    }
}

@media (max-width: 600px) {
    .order-summ {
        h1 {
            min-width: 225px;
        }
    }

}